$mobile-breakpoint: 1000px;

.filter-result {
  display: flex;
  align-items: center;
  min-height: 300px;
  justify-content: center;

  @media (max-width: $mobile-breakpoint) {
    flex-direction: column;
  }

  .filtered-item {
    padding: 0 7px;
    border: 1px solid #a2a2a2;
    border-radius: 4px;
    margin: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
  }

  .results-wrapper {
    display: flex;
    flex-flow: row wrap;

    @media (max-width: $mobile-breakpoint) {
      padding: 10px;
    }
  }

  .delete {
    border: none;
    background-color: transparent;
    margin-left: 7px;
    padding: 0;
  }

  .clear-all-btn {
    margin-left: 5px;
  }
}