.filter-navigation {
  margin: 10px;
  border: 2px solid grey;
  border-radius: 4px;
  padding: 2px;

  .desktop-nav {
    display: flex;
    flex-flow: row wrap;
    @media (max-width: 1000px) {
      display: none;
    }
  }
  .mobile-nav {
    display: none;

    @media (max-width: 1000px) {
      display: flex;
    }

    #popover-more-items-popover {
      margin: 2px;
    }

    > div {
      width: 100%;
      max-width: 33.333%;

      > button {
        width: calc(100% - 4px);
      }
    }
  }
}