.filter-group {
  > button {
    text-transform: capitalize;
    margin: 2px;
  }
}

.filter-group-mobile {
  .mobile-collapse-toggle {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid grey;
    width: 100%;
    display: block;
    position: relative;
    text-transform: capitalize;

    > span {
      position: absolute;
      right: 6px;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  .buttons-wrapper {
    padding-bottom: 10px !important;
  }
}

.popover {
  @media (max-width: 1000px) {
    width: 100%;
    max-width: calc(100% - 10px);
  }

  .popover-body {
    .items-wrapper {
      display: flex;
      flex-flow: row wrap;
    }

    .buttons-wrapper {
      display: flex;
      justify-content: space-between;
      padding: 20px 2px 0;
    }
  }
}